import React, { useState, useEffect } from 'react';
import { Form, Button, Card, Alert } from 'react-bootstrap';
import { PublicKey } from '@solana/web3.js';
import TokenService from '../services/token-service';
import { useWallet } from '../contexts/WalletContext';

const TokenTransfer = ({ tokenAddress }) => {
  const { connected, publicKey } = useWallet();
  const [recipient, setRecipient] = useState('');
  const [amount, setAmount] = useState('');
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState(null);
  const [success, setSuccess] = useState(null);
  const [isValidAddress, setIsValidAddress] = useState(true);
  const [balance, setBalance] = useState(null);
  
  useEffect(() => {
    if (connected && publicKey) {
      loadBalance();
    }
  }, [connected, publicKey, tokenAddress]);

  const loadBalance = async () => {
    try {
      if (tokenAddress === 'native') {
        const bal = await TokenService.getNativeBalance(publicKey.toString());
        setBalance(bal);
      } else if (tokenAddress) {
        const bal = await TokenService.getTokenBalance(tokenAddress, publicKey.toString());
        setBalance(bal);
      }
    } catch (err) {
      console.error('Error loading balance:', err);
    }
  };
  
  const validateAddress = (address) => {
    try {
      if (!address) return true;
      new PublicKey(address);
      return true;
    } catch (err) {
      return false;
    }
  };

  const handleRecipientChange = (e) => {
    const value = e.target.value;
    setRecipient(value);
    setIsValidAddress(validateAddress(value));
    setError(null);
  };

  const handleAmountChange = (e) => {
    const value = e.target.value;
    setAmount(value);
    setError(null);
    
    const numValue = parseFloat(value);
    if (!isNaN(numValue) && balance !== null && numValue > balance) {
      setError('Insufficient balance');
    }
  };
  
  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!connected || !publicKey) {
      setError('Wallet not connected');
      return;
    }
    
    if (!recipient || !amount) {
      setError('Please fill in all fields');
      return;
    }
    
    if (!validateAddress(recipient)) {
      setError('Invalid Solana address');
      return;
    }
    
    const amountValue = parseFloat(amount);
    if (isNaN(amountValue) || amountValue <= 0) {
      setError('Invalid amount');
      return;
    }

    if (amountValue > balance) {
      setError('Insufficient balance');
      return;
    }
    
    setIsSubmitting(true);
    setError(null);
    setSuccess(null);
    
    try {
      let result;
      
      if (tokenAddress === 'native') {
        result = await TokenService.transferNativeSOL(
          publicKey.toString(),
          recipient,
          amountValue
        );
      } else {
        result = await TokenService.transferToken(
          tokenAddress,
          publicKey.toString(),
          recipient,
          amountValue
        );
      }
      
      if (result.status === 'confirmed') {
        setSuccess('Transfer successful');
        setRecipient('');
        setAmount('');
        setTimeout(loadBalance, 2000);
      } else {
        setError('Transfer failed');
      }
    } catch (err) {
      console.error('Transfer error:', err);
      setError(err.message || 'Transfer failed');
    } finally {
      setIsSubmitting(false);
    }
  };
  
  return (
    <Card>
      <Card.Body>
        <Card.Title>Transfer {tokenAddress === 'native' ? 'SOL' : 'Tokens'}</Card.Title>
        
        {error && <Alert variant="danger">{error}</Alert>}
        {success && <Alert variant="success">{success}</Alert>}
        
        <div className="mb-4">
          <div className="d-flex justify-content-between align-items-center">
            <div>
              <strong>Balance</strong>
              <div className="text-muted">{balance !== null ? `${balance} ${tokenAddress === 'native' ? 'SOL' : 'tokens'}` : 'Loading...'}</div>
            </div>
          </div>
        </div>
        
        <Form onSubmit={handleSubmit}>
          <Form.Group className="mb-4">
            <Form.Label>Recipient Address</Form.Label>
            <Form.Control
              type="text"
              value={recipient}
              onChange={handleRecipientChange}
              disabled={isSubmitting}
              isInvalid={!isValidAddress && recipient !== ''}
              className="font-monospace"
              placeholder="Enter recipient's Solana address"
            />
          </Form.Group>
          
          <Form.Group className="mb-4">
            <Form.Label>Amount</Form.Label>
            <div className="input-group">
              <Form.Control
                type="number"
                step="0.000001"
                min="0"
                value={amount}
                onChange={handleAmountChange}
                disabled={isSubmitting}
                placeholder="Enter amount to transfer"
              />
              <span className="input-group-text">{tokenAddress === 'native' ? 'SOL' : 'tokens'}</span>
            </div>
          </Form.Group>
          
          <Button 
            variant="primary" 
            type="submit"
            disabled={isSubmitting || !isValidAddress || (amount && parseFloat(amount) > balance)}
            className="w-100"
          >
            {isSubmitting ? 'Processing...' : 'Transfer'}
          </Button>
        </Form>
      </Card.Body>
    </Card>
  );
};

export default TokenTransfer; 