import React, { createContext, useContext, useState, useEffect } from 'react';
import {
  ConnectionProvider,
  WalletProvider,
  useWallet as useSolanaWallet,
  useConnection
} from '@solana/wallet-adapter-react';
import { WalletAdapterNetwork } from '@solana/wallet-adapter-base';
import {
  PhantomWalletAdapter,
  SolflareWalletAdapter
} from '@solana/wallet-adapter-wallets';
import { WalletModalProvider, WalletMultiButton } from '@solana/wallet-adapter-react-ui';
import { clusterApiUrl } from '@solana/web3.js';
import { AlebrijeError, ErrorTypes } from '../utils/error-handler';

// Import wallet adapter CSS
import '@solana/wallet-adapter-react-ui/styles.css';

// Create a simpler context structure
const AlebrijeWalletContext = createContext(null);

export function WalletContextProvider({ children }) {
  // Network configuration
  const [network, setNetwork] = useState(WalletAdapterNetwork.Devnet);
  const endpoint = clusterApiUrl(network);
  
  // Get wallet adapters
  const wallets = [
    new PhantomWalletAdapter(),
    new SolflareWalletAdapter()
  ];
  
  const switchNetwork = (newNetwork) => {
    if (Object.values(WalletAdapterNetwork).includes(newNetwork)) {
      setNetwork(newNetwork);
      return true;
    }
    return false;
  };
  
  // Create a wrapper to provide our custom context
  const AlebrijeWalletContextProvider = ({ children }) => {
    const { publicKey, connected, connecting, disconnect, select, wallet } = useSolanaWallet();
    const { connection } = useConnection();
    
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);
    
    const contextValue = {
      publicKey,
      connected,
      connecting,
      connection,
      currentAccount: publicKey?.toString() || null,
      loading,
      error,
      walletName: wallet?.name || null,
      disconnectWallet: async () => {
        try {
          setLoading(true);
          await disconnect();
          return true;
        } catch (err) {
          setError(err);
          throw err;
        } finally {
          setLoading(false);
        }
      },
      connectWallet: async (walletName) => {
        try {
          setLoading(true);
          if (walletName) {
            select(walletName);
          }
          return connected;
        } catch (err) {
          setError(err);
          throw err;
        } finally {
          setLoading(false);
        }
      },
      switchNetwork,
      network
    };
    
    return (
      <AlebrijeWalletContext.Provider value={contextValue}>
        {children}
      </AlebrijeWalletContext.Provider>
    );
  };
  
  return (
    <ConnectionProvider endpoint={endpoint}>
      <WalletProvider wallets={wallets} autoConnect>
        <WalletModalProvider>
          <AlebrijeWalletContextProvider>
            {children}
          </AlebrijeWalletContextProvider>
        </WalletModalProvider>
      </WalletProvider>
    </ConnectionProvider>
  );
}

// Custom hook to use our wallet context
export function useAlebrijeWallet() {
  const context = useContext(AlebrijeWalletContext);
  
  if (!context) {
    throw new Error('useAlebrijeWallet must be used within a WalletContextProvider');
  }
  
  return context;
}

// For backward compatibility
export const useWallet = useAlebrijeWallet;

// Export the WalletMultiButton component for easy access
export { WalletMultiButton }; 